import React, { Component, createElement } from 'react';
//Data
import timedata from "./timedata.json"

class TimeSlot extends Component{    


    render(){
      const { month, id } =this.props
      const data = timedata.filter((month) => {
        if (month.id === id){
          return month
        }
      })
      const events = data[0].events
      const reflect = (id, content) => {
        const body = document.getElementById(id)
        const children = document.getElementsByClassName("reflection-content")
        console.log(children.length)
        if (children.length > 0){
          children[0].remove()
        }
        
        const reflection = document.createElement('div')
        reflection.className = "reflection-content"
        reflection.textContent = content
        body.appendChild(reflection)     
        
         
        //<div id="reflection-content"><h4>Reflection: </h4>{event.reflection}</div>                              
      }
      return(
        <div id="time-slot">
          <div className="month-container">
            <div id="month">{month}</div>
          </div>
          <div className="dates-container">
            <div className="event-container">
              {events.map((event) => {                
                return <a id="event" onClick={() => reflect(event.id+" reflection", event.reflection)}>
                          <a className="event-header" >
                            <div id={"date"}>{event.date}</div>
                            <div className={"event-info"}>
                              <div>{event.event}</div>  
                            </div>
                          </a>
                          <div className="reflection" id={event.id+" reflection"}>

                          </div>  
                       </a>
              })}
            </div>
          </div>

        </div>
        );
    }
}


export default TimeSlot


