import React, { Component } from 'react';

class Intro extends Component{    
    render(){
        return(
        <div className="intro-box">
            <div class="intro">
              <div id="herman-picture"></div>
              <div className="herman-info-box">
                <h1>
                  About:
                </h1>
                <h2>Hai, I'm Herman and this is my Github Home-page</h2>
                <h5>
                  This is a page where I aim to chronicle my progress as a developer. 
                </h5>
                <h5>
                  I graduated with a degree in mechanical engineering, and tinkered with many small robotic projects during my time at school, but i never dove deep into the field. However, I always found the mad process of programming the machines to accomplish whatever I had envisioned to be a pretty fun and rewarding process. However, I never considered pursuing anything related to computer science as a profession, and actually always thought that since I didnt start programming until my university days, I was simply too far behind and would never catch up in time.
                </h5>
                <h5>
                  Flash forward to a couple years out of graduation and I really begin to miss the process of programming and engineering design. Taking a bit of a leap of faith I decided to fully dive into web-development and see how far I could go.
                </h5>
                <h5>
                  I hope to share the projects and assignmnets i've worked on throughout my time jumping into web dev. Some technologies i've worked with now include React, NodeJS, Immutable JS, Sass, and Typescript. Currently this page only serves as a short journal to show the process i've been taking, and as such the timeline will b the main focus. In the future, once i feel like i have a good collection of projects worth showcasing, i hope to shift focus to my work rather than my journey.
                </h5>
                <h5>
                  My goal is to become a full-time Full-Stack developer, and I'm taking Udacity nanodegrees to try to achieve that.
                </h5>
                <h5>
                  In my spare time i tinker with some electronics, and produce a bit of music. For whatever reason, i've also really gotten into Street Fighter lately.
                </h5>
              </div>
            </div>

        </div>
        );
    }
}


export default Intro
